/* eslint-disable max-len */

import { Ensure, Logger } from '../common';
import HttpClient from './b8one.httpClient';

/**
 * Cart Abstraction class
 */
class CartService {
  /**
   * Cart constructor
   */
  constructor() {
    this._VTEX_API_ENDPOINT = '/api/checkout/pub/orderForm';

    this._logger = new Logger('Cart service');
    this._httpClient = new HttpClient();
  }

  /**
   * Get current cart
   * @return {*} Cart object
   */
  async getCurrent() {
    return await this._httpClient.get(this._VTEX_API_ENDPOINT);
  }

  /**
   * Add item to cart
   * @param {*} skuId - Product sku Id (SKU)
   * @param {*} productQuantity - Product Quantity
   * @param {*} sellerId - Seller id
   */
  async add(skuId, productQuantity, sellerId = 1) {
    // const payload = this._payloadFactory(skuId, productQuantity, sellerId);

    if (!Ensure.isUndefined(skuId)) {
      const current = await this.getCurrent();
      const indexFound = current.items.findIndex(x => x.id === skuId);

      if (indexFound > -1) {
        productQuantity =
          parseInt(productQuantity) +
          parseInt(current.items[indexFound].quantity);
      }

      const endpointUrl = `${this._VTEX_API_ENDPOINT}/${current.orderFormId}/items`;

      const payload = {
        orderItems: [
          {
            seller: sellerId.toString(),
            quantity: parseInt(productQuantity),
            id: skuId.toString(),
          },
        ],
      };

      return this._httpClient.patch(endpointUrl, payload);
    }
  }

  async checkoutCRUD(skus) {
    let { items } = await window.vtexjs.checkout.getOrderForm();

    const skusCustomProducts = this.getCustomProducts(skus);

    skus = skus.filter(sku => !sku.isCustom);
    skus = [...skus, ...skusCustomProducts];

    const skuToIgnore = [19, 698, 25, 26];

    const indexToRemove = items.reduce((indexList, item, index) => {
      const customProductSKUs = [25, 26];

      if (customProductSKUs.includes(parseInt(item.id))) {
        return [...indexList, index];
      }

      return indexList;
    }, []);

    if (indexToRemove.length > 0) {
      const response = await this.removeMoreProducts(indexToRemove);

      response && (items = response.items);
    }

    const productsToAdd = skus
      .filter(sku => sku.quantity > 0)
      .filter(sku => {
        let index = items.findIndex(
          item => parseInt(item.productId) === parseInt(sku.productId)
        );

        return index === -1 && sku.quantity > 0;
      })
      .map(sku => ({
        quantity: sku.quantity,
        seller: 1,
        id: sku.skuId || sku.id,
      }));

    console.log('productsToAdd', productsToAdd);

    const productsToUpdate = skus
      .filter(sku => sku.quantity > 0)
      .filter(sku => {
        const id = sku.skuId || sku.id;

        let index = items.findIndex(item => parseInt(item.id) === parseInt(id));

        if (index !== -1) {
          const sameSku = parseInt(items[index].id) === parseInt(sku.id);
          const diffQuantity =
            parseInt(items[index].quantity) !== parseInt(sku.quantity);

          return sameSku && diffQuantity && sku.quantity > 0;
        }

        return false;
      })
      .map(sku => {
        const id = sku.skuId || sku.id;

        let index = items.findIndex(item => parseInt(item.id) === parseInt(id));

        return {
          index,
          quantity: sku.quantity,
        };
      });

    console.log('productsToUpdate', productsToUpdate);

    const productsToReplaceSKU = skus
      .filter(
        sku =>
          sku.quantity > 0 &&
          !skuToIgnore.includes(parseInt(sku.skuId || sku.id))
      )
      .filter(sku => {
        const index = items.findIndex(
          item => parseInt(item.productId) === parseInt(sku.productId)
        );

        if (index !== -1) {
          const diffSku = parseInt(items[index].id) !== parseInt(sku.skuId);

          return diffSku;
        }

        return false;
      })
      .map(sku => {
        const index = items.findIndex(
          item => parseInt(item.productId) === parseInt(sku.productId)
        );

        const oldSKU = {
          index,
          seller: 1,
          quantity: 0,
        };

        const newSKU = {
          seller: 1,
          id: sku.skuId,
          quantity: sku.quantity,
        };

        return [oldSKU, newSKU];
      })
      .reduce((itemsList, current) => {
        const [oldSKU, newSKU] = current;

        return [...itemsList, oldSKU, newSKU];
      }, []);

    console.log('productsToReplaceSKU', productsToReplaceSKU);

    const productsToRemove = skus
      .filter(sku => sku.quantity < 1)
      .filter(sku => {
        const id = sku.skuId || sku.id;

        let index = items.findIndex(item => parseInt(item.id) === parseInt(id));

        if (index !== -1) {
          return sku.quantity == 0;
        }

        return false;
      })
      .map(sku => {
        const id = sku.skuId || sku.id;

        let index = items.findIndex(item => parseInt(item.id) === parseInt(id));

        return {
          index,
          quantity: 0,
        };
      });

    console.log('productsToRemove', productsToRemove);

    if (productsToUpdate[0]) {
      await window.vtexjs.checkout.updateItems(productsToUpdate, null, false);
    }

    if (productsToReplaceSKU[0]) {
      await window.vtexjs.checkout.replaceSKU(productsToReplaceSKU);
    }

    if (productsToRemove[0]) {
      await window.vtexjs.checkout.removeItems(productsToRemove);
    }

    if (productsToAdd[0]) {
      await window.vtexjs.checkout.addToCart(productsToAdd, null);
    }
  }

  getCustomProducts(skus) {
    const customProducts = {};

    skus
      .filter(({ isCustom }) => isCustom === true)
      .forEach(({ skuId, quantity, customProductItem }) => {
        let currentCustomProduct = customProducts[skuId];

        const customProductQuantity =
          parseInt(customProductItem.quantity) * parseInt(quantity);

        if (currentCustomProduct) {
          const newQuantity =
            parseInt(currentCustomProduct.quantity) + customProductQuantity;

          currentCustomProduct = {
            ...currentCustomProduct,
            quantity: newQuantity,
          };
        } else {
          currentCustomProduct = {
            ...customProductItem,
            quantity: customProductQuantity,
            isCustom: true,
          };
        }

        customProducts[skuId] = currentCustomProduct;
      });

    return Object.values(customProducts);
  }

  async getProductIndexInOrderForm(productId) {
    const { items } = await this.getCurrent();

    const index = items.findIndex(item => item.productId == productId);

    if (index !== -1) return index;

    return;
  }

  /**
   * Remove item to cart
   * @param {JSON} itemIndex - Product item index to be removed
   */
  async remove(itemIndex) {
    const current = await this.getCurrent();
    const endpointUrl = `${this._VTEX_API_ENDPOINT}/${current.orderFormId}/items`;
    const payload = {
      orderItems: [
        {
          index: itemIndex,
          quantity: 0,
        },
      ],
      expectedOrderFormSections: ['items'],
      noSplitItem: true,
    };

    await this._httpClient.patch(endpointUrl, payload);
  }

  /**
   * Remove items of cart
   * @param {Array} itemsIndex - Products items index to be removed
   */
  async removeMoreProducts(itemsIndex) {
    const current = await this.getCurrent();
    const endpointUrl = `${this._VTEX_API_ENDPOINT}/${current.orderFormId}/items`;
    const payload = {
      orderItems: itemsIndex.map(index => ({
        index,
        quantity: 0,
      })),
      expectedOrderFormSections: ['items'],
      noSplitItem: true,
    };

    return await this._httpClient.patch(endpointUrl, payload);
  }

  async updateProduct(skuId, quantity, seller = 1) {
    const newSKU = {
      seller,
      quantity,
      id: skuId,
    };

    return await this.updateProducts([newSKU]);
  }

  async updateProducts(skus) {
    const { items, orderFormId } = await this.getCurrent();

    skus = skus
      .map(({ id, quantity, seller }) => {
        const index = items.findIndex(
          item => parseInt(item.id) === parseInt(id)
        );

        return {
          id,
          index,
          quantity,
          seller: seller || 1,
        };
      })
      .filter(sku => sku.index !== -1);

    if (skus.length > 0) {
      const payload = {
        orderItems: skus,
      };

      return await this._httpClient.patch(
        `/api/checkout/pub/orderForm/${orderFormId}/items`,
        payload
      );
    }
  }

  async simulateOrderForm(postalCode, country = 'BRA') {
    try {
      await vtexjs.checkout.getOrderForm();

      const orderForm = await vtexjs.checkout.calculateShipping({
        postalCode,
        country,
      });

      return orderForm;
    } catch (error) {
      console.error('Error in simulateOrderForm', error);
    }
  }

  async simulateShipping(items, postalCode, country = 'BRA') {
    const orderFormSimulated = await vtexjs.checkout.simulateShipping(
      items,
      postalCode,
      country
    );

    return orderFormSimulated;
  }

  async updateCheckoutTransporterSLA(logisticsInfo) {
    await vtexjs.checkout.getOrderForm();

    const { shippingData } = vtexjs.checkout.orderForm;

    shippingData.logisticsInfo = logisticsInfo;

    await vtexjs.checkout.sendAttachment('shippingData', shippingData);
  }

  async updateCheckoutAddress(address) {
    console.log(address);
    await vtexjs.checkout.getOrderForm();

    const { shippingData } = vtexjs.checkout.orderForm;

    shippingData.availableAddresses = [
      {
        ...shippingData.address,
        ...address,
      },
    ];

    shippingData.selectedAddresses = [
      {
        ...shippingData.address,
        ...address,
      },
    ];

    shippingData.address = {
      ...shippingData.address,
      ...address,
    };

    return await vtexjs.checkout.sendAttachment('shippingData', shippingData);
  }

  async setMultipleCustomDataFields(appName, fields) {
    const { orderFormId } = await this.getCurrent();

    const endpoint = `/api/checkout/pub/orderForm/${orderFormId}/customData/${appName}`;

    try {
      return await this._httpClient.put(endpoint, fields);
    } catch (error) {
      console.error('setMultipleCustomDataFields', error);
    }

    return;
  }

  async setSingleCustomDataField(appName, appFieldName, appFieldValue) {
    const { orderFormId } = await this.getCurrent();

    const endpoint = `/api/checkout/pub/orderForm/${orderFormId}/customData/${appName}/${appFieldName}`;

    const payload = { value: appFieldValue };

    try {
      return await this._httpClient.put(endpoint, payload);
    } catch (error) {
      console.error('setSingleCustomDataField', error);
    }

    return;
  }

  async removeSingleCustomDataField(appName, appFieldName) {
    const { orderFormId } = await this.getCurrent();

    const endpoint = `/api/checkout/pub/orderForm/${orderFormId}/customData/${appName}/${appFieldName}`;

    const payload = { value: null };

    try {
      return await this._httpClient.delete(endpoint, payload);
    } catch (error) {
      console.error('removeSingleCustomDataField', error);
    }

    return;
  }

  /**
   * Cart's payload method factory
   * @param {*} skuId - Product Id (SKU)
   * @param {*} productQuantity - Product Quantity
   * @param {*} sellerId - Seller id
   * @return {JSON} cart paylod
   */
  _payloadFactory(skuId, productQuantity, sellerId = 1) {
    return {
      id: skuId,
      quantity: productQuantity,
      seller: sellerId,
    };
  }
}

export default CartService;
