// Dependencies
import IMask from 'imask';

// Utils
import { REAL } from 'Utils/currency/';
import SKUS_TO_IGNORE from 'Utils/skusToIgnore';

// Services
import CartService from 'Services/b8one.cartService.js';

import ModalComponent from 'Components/b8one.modalComponent.js';

class InsuranceChangePage {
  constructor() {
    this.products = {};
    this.cartService = new CartService();

    window.InsuranceChangePage = this;

    this.valueInsurance = REAL('R$ 0,00');
    this.totalInsurance = REAL('R$ 0,00');
    this.totalOrderSummary = REAL('R$ 0,00');
    this.totalTransporterSelected = REAL('R$ 0,00');

    this.totalInsuranceElement;
    this.totalTransporterSelectedElement;

    this.lastInsurancePercentage = 'R$ 0,00';

    this.rejectInsuranceModalInstance = null;

    this.orderForm = {};
    this.orderDetails = {};

    this.init();
  }

  async init() {
    if (location.pathname !== '/_secure/seguro/contratacao') {
      this.setHandledButtonSkipStepClick();
    }

    this.orderForm = await this.cartService.getCurrent();

    if (location.pathname === '/_secure/seguro/contratacao') {
      const orderID = location.search.match(/[\d\-\d{2}]+/g)[0];

      const data = await (await fetch(`/api/oms/pvt/orders/${orderID}`)).json();

      this.orderForm = data;
      this.orderDetails = data;
    }

    if (
      location.pathname === '/seguro' ||
      location.pathname === '/_secure/seguro/contratacao'
    ) {
      this.setTotalTransporterSelected();

      this.getProductstInOrderForm();
      this.getProductsInCustomData();

      this.mapProducts();

      this.getInsuranceDataInCustomData();

      this.setTotalOrderSummary();
      this.setValueInsurance();

      this.setTotalInsuranceElement();
      this.setTotalOrderSummaryElement();
      this.setValueInsuranceElement();

      this.setInputMasks();
      this.updateProducts();

      this.setHandledProducts();

      this.setHandledButtonSaveClick();
    } else {
      this.initRejectInsuranceModal();
      this.setHandledButtonRejectInsuranceClick();
      this.setHandledButtonConfirmRejectInsuranceClick();
    }
  }

  setTotalTransporterSelected() {
    const { customData } = this.orderForm;

    if (!customData) return;

    customData.customApps
      .filter(customApp => customApp.id === 'selectedcourier')
      .forEach(customApp => {
        const { courier_data } = customApp.fields;

        const transporter = JSON.parse(courier_data);

        const { priceFormatted } = transporter.deliveryTypeSelected;

        this.totalTransporterSelected = this.totalTransporterSelected.add(
          priceFormatted
        );
      });
  }

  setTotalInsuranceElement() {
    const totalInsuranceElement = document.querySelector('.js-total-insurance');

    totalInsuranceElement.textContent = this.totalInsurance.format();

    this.totalInsuranceElement = totalInsuranceElement;
  }

  setTotalOrderSummaryElement() {
    const totalOrderSummaryElement = document.querySelector(
      '.js-total-order-summary'
    );

    totalOrderSummaryElement.textContent = this.totalOrderSummary.format();
  }

  setTotalOrderSummary() {
    this.totalOrderSummary = this.totalOrderSummary
      .add(this.totalTransporterSelected.format())
      .add(this.totalInsurance.multiply(0.01).format());
  }

  setValueInsurance() {
    this.valueInsurance = this.valueInsurance.add(
      this.totalInsurance.multiply(0.01).format()
    );

    this.lastInsurancePercentage = this.valueInsurance.format();
  }

  setValueInsuranceElement() {
    const valueInsuranceElement = document.querySelector('.js-value-insurance');

    valueInsuranceElement.textContent = this.valueInsurance.format();
  }

  mapProducts() {
    const products = [...document.querySelectorAll('.product')];

    products.forEach(product => {
      const { productId } = product.dataset;

      this.products[productId] = {
        productId,
        totalInsurance: 0,
        totalInsuranceFormatted: 'R$ 0,00',
      };
    });
  }

  setHandledProduct = product => {
    const { productId } = product.dataset;
    const productInsuranceInput = product.querySelector('.product__value');

    if (productInsuranceInput) {
      let productOldInsuranceValue = productInsuranceInput.value;

      productInsuranceInput.addEventListener('input', ({ target }) => {
        const { value } = target;

        let newTotalInsuranceQuantity = this.totalInsurance
          .subtract(productOldInsuranceValue)
          .add(value);

        this.totalOrderSummary = this.totalOrderSummary.subtract(
          this.lastInsurancePercentage
        );

        this.valueInsurance = this.valueInsurance.subtract(
          this.lastInsurancePercentage
        );

        this.totalInsuranceElement.textContent = newTotalInsuranceQuantity.format();

        this.totalInsurance = newTotalInsuranceQuantity;

        this.lastInsurancePercentage = this.totalInsurance
          .multiply(0.01)
          .format();

        this.totalOrderSummary = this.totalOrderSummary.add(
          this.lastInsurancePercentage
        );

        this.valueInsurance = this.valueInsurance.add(
          this.lastInsurancePercentage
        );

        productOldInsuranceValue = value;

        this.products[productId] = {
          ...this.products[productId],
          totalInsurance: REAL(value).value,
          totalInsuranceFormatted: REAL(value).format(),
        };

        this.setTotalOrderSummaryElement();
        this.setValueInsuranceElement();
      });
    }
  };

  setHandledProducts() {
    const products = [...document.querySelectorAll('.product')];

    products.forEach(this.setHandledProduct);
  }

  getProductstInOrderForm() {
    const { items } = this.orderForm;

    const insuranceItemsTable = document.querySelector(
      '.insurance-items__table'
    );

    const productsHTML = items
      .filter(({ id }) => !SKUS_TO_IGNORE.includes(parseInt(id)))
      .map(({ name, imageUrl, productId, skuName, quantity }) => {
        const productName = name.replace(skuName, '');
        const description = '';

        const productHTML = `
          <div class="table__row table__row--hidden">
            ${this.createProduct({
              imageUrl,
              productId,
              productName,
              description,
              quantity,
            })}
          </div>
        `;

        return productHTML;
      });

    insuranceItemsTable.innerHTML += productsHTML.join('');
  }

  getProductsInCustomData() {
    const { customData } = this.orderForm;

    if (!customData) return;

    customData.customApps
      .filter(customApp => customApp.id === 'selecteditems')
      .map(customApp => {
        const { products_data } = customApp.fields;
        const products = JSON.parse(products_data);
        const customProducts = products.filter(({ isCustom }) => isCustom);

        const insuranceItemsTable = document.querySelector(
          '.insurance-items__table'
        );

        const productsHTML = customProducts
          .map(productData => {
            const productHTML = `
              <div class="table__row table__row--hidden">
                ${this.createCustomProduct(productData)}
              </div>
            `;

            return productHTML;
          })
          .join('');

        insuranceItemsTable.innerHTML += productsHTML;
      });
  }

  updateProducts() {
    Object.values(this.products).forEach(
      ({ productId, totalInsuranceFormatted }) => {
        const productElement = document.querySelector(
          `[data-product-id='${productId}']`
        );

        const productInputElement = productElement.querySelector(
          '.js-product-insurance-input'
        );

        productInputElement.value = totalInsuranceFormatted;
      }
    );
  }

  createProduct({ productId, productName, description, imageUrl, quantity }) {
    const productHTML = `
      <div 
        class="product"
        data-product-id="${productId}"
      >
        <div class="product__container">
          <figure class="product__figure">
            <img 
              class="product__image" 
              alt="${productName}"
              src="${imageUrl}"
            />
          </figure>
          
          <div class="product__group">
            <h3 class="product__name">${productName} (${quantity} un.)</h3>
            
            <div class="product__attributes">
              <p class="attributes__item">${description}</p>
            </div>
          </div>
        </div>
      
        <input 
          type="tel"
          placeholder="R$ 0,00"
          class="product__value js-product-insurance-input"
        />
      </div>
    `;

    return productHTML;
  }

  createProducts(products) {
    products = products
      .map(product => {
        return `
        <div class="table__row">
          ${this.createProduct(product)}
        </div>
      `;
      })
      .join('');

    return products;
  }

  createCustomProduct(productData) {
    const { quantity, customAttributes, productId } = productData;

    const customProduct = `
      <div 
        class="product"
        data-product-quantity="${quantity}"
        data-product-id="${productId}"
      >
        <div class="product__container">
          <div class="product__icon-container">
            <i class="far fa-box product__icon"></i>
          </div>
          
          <div class="product__group">
            <h3 class="product__name">
              ${customAttributes['product-name']} (${quantity} un.)
            </h3>
            
            <div class="product__attributes">
              <p class="attributes__item">
                ${customAttributes['product-height']}cm x 
                ${customAttributes['product-width']}cm x 
                ${customAttributes['product-depth']}cm
              </p>
            </div>
          </div>
        </div>

        <input 
          type="tel"
          placeholder="R$ 0,00"
          class="product__value js-product-insurance-input"
        />
      </div>
    `;

    return customProduct;
  }

  addProductsToDOM() {
    const insuranceItemsTable = document.querySelector(
      '.insurance-items__table'
    );

    insuranceItemsTable.innerHTML += this.createProducts();
  }

  setInputMasks() {
    [...document.querySelectorAll('.js-product-insurance-input')].forEach(
      productInsuranceInput => {
        IMask(productInsuranceInput, {
          mask: 'R$ num',
          blocks: {
            num: {
              // nested masks are available!
              mask: Number,
              thousandsSeparator: '.',
              scale: 2,
            },
          },
        });
      }
    );
  }

  setHandledButtonSaveClick() {
    const buttonSave = document.querySelector('.js-button-save');
    const buttonNext = document.querySelector('.footer__buttom-submit');

    buttonSave.addEventListener('click', async () => {
      document.dispatchEvent(window.initSaveCustomDataEvent);

      await this.saveData();

      document.dispatchEvent(window.endSaveCustomDataEvent);
    });

    buttonNext &&
      buttonNext.addEventListener('click', async e => {
        e.preventDefault();
        buttonNext.classList.toggle('js-loading');

        await this.saveData();

        buttonNext.classList.toggle('js-loading');

        if (location.pathname === '/_secure/seguro/contratacao') {
          window.location.assign('/checkout/#/payment');
        } else {
          window.location.assign('/resumo');
        }
      });
  }

  getInsuranceDataInCustomData() {
    const { customData } = this.orderForm;

    if (!customData) return;

    customData.customApps
      .filter(customApp => customApp.id === 'insurance')
      .forEach(customApp => {
        let { items, total } = customApp.fields;

        items = ['skipped', 'none'].includes(items) ? items : JSON.parse(items);

        if (items && !['skipped', 'none'].includes(items)) {
          items = Object.entries(items)
            .filter(([key, value]) => key in this.products)
            .reduce((obj, [key, value]) => ({ ...obj, [key]: value }), {});

          this.products = {
            ...this.products,
            ...items,
          };
        }

        if (total) {
          this.totalInsurance = REAL(total);
        }
      });
  }

  async saveData() {
    try {
      const insuranceFieldsForCustomData = {
        items: JSON.stringify(this.products),
        total: this.totalInsurance.format(),
      };

      const insuranceSKU = {
        id: 35,
        quantity: 1,
        seller: '1',
      };

      if (this.totalInsurance.intValue > 0) {
        await window.vtexjs.checkout.getOrderForm();
        this.orderForm = await window.vtexjs.checkout.addToCart(
          [insuranceSKU],
          null
        );

        await this.updateInsuranceSKUPrice(
          this.totalInsurance.multiply(0.01).intValue
        );
      }

      if (location.pathname === '/_secure/seguro/contratacao') {
        await this.updateCheckoutAddress();
      }

      await Promise.all([
        this.cartService.setMultipleCustomDataFields(
          'insurance',
          insuranceFieldsForCustomData
        ),
        this.cartService.setSingleCustomDataField(
          'orderinfo',
          'current_step',
          'insurance-change-2'
        ),
      ]);
    } catch (error) {
      console.log('saveData', error);
    }
  }

  async skipInsuranceStep() {
    try {
      let insuranceSKUIndex;

      this.orderForm.items.forEach(({ id }, index) => {
        if (parseInt(id) === 35) {
          insuranceSKUIndex = index;
        }
      });

      if (insuranceSKUIndex) {
        await this.cartService.remove(insuranceSKUIndex);
      }

      await Promise.all([
        this.cartService.removeSingleCustomDataField('insurance', 'total'),
        this.cartService.setSingleCustomDataField(
          'insurance',
          'items',
          'skipped'
        ),
        this.cartService.setSingleCustomDataField(
          'orderinfo',
          'current_step',
          'insurance-change-1'
        ),
      ]);

      return true;
    } catch (error) {
      console.error(error);
    }

    return;
  }

  async noContractInsurance() {
    try {
      let insuranceSKUIndex;

      this.orderForm.items.forEach(({ id }, index) => {
        if (parseInt(id) === 35) {
          insuranceSKUIndex = index;
        }
      });

      if (insuranceSKUIndex) {
        await this.cartService.remove(insuranceSKUIndex);
      }

      await Promise.all([
        this.cartService.removeSingleCustomDataField('insurance', 'total'),
        this.cartService.setSingleCustomDataField('insurance', 'items', 'none'),
        this.cartService.setSingleCustomDataField(
          'orderinfo',
          'current_step',
          'insurance-change-1'
        ),
      ]);

      return true;
    } catch (error) {
      console.error(error);
    }

    return;
  }

  setHandledButtonSkipStepClick() {
    const buttonSkipStep = document.querySelector('.js-button-later-contract');

    buttonSkipStep.addEventListener('click', async e => {
      e.preventDefault();

      await this.skipInsuranceStep();

      window.location.assign('/resumo');
    });
  }

  initRejectInsuranceModal() {
    const rejectInsuranceModal = document.querySelector(
      '.reject-insurance-modal'
    );

    if (rejectInsuranceModal) {
      this.rejectInsuranceModalInstance = new ModalComponent({
        modal: rejectInsuranceModal,
        modalShowCLass: 'wrapper-modal--show',
      });

      const buttonCloseModal = rejectInsuranceModal.querySelector(
        '.modal__close'
      );

      const buttonConfirm = rejectInsuranceModal.querySelector(
        '.button--primary'
      );

      buttonCloseModal.addEventListener('click', () => {
        this.rejectInsuranceModalInstance.close();
      });

      buttonConfirm.addEventListener('click', () => {
        buttonConfirm.classList.add('js-loading', 'button--disabled');

        setTimeout(() => {
          window.location.assign('/resumo');
        }, 1500);
      });
    }
  }

  setHandledButtonRejectInsuranceClick() {
    const buttonRejectInsurance = document.querySelector(
      '.js-button-no-contract'
    );

    buttonRejectInsurance.addEventListener('click', e => {
      e.preventDefault();

      this.rejectInsuranceModalInstance.show();
    });
  }

  setHandledButtonConfirmRejectInsuranceClick() {
    const rejectInsuranceModal = document.querySelector(
      '.reject-insurance-modal'
    );

    const buttonConfirm = rejectInsuranceModal.querySelector('.modal__button');

    buttonConfirm.addEventListener('click', async e => {
      e.preventDefault();

      await this.noContractInsurance();

      window.location.assign('/resumo');
    });
  }

  async updateInsuranceSKUPrice(price) {
    const { orderFormId } = this.orderForm;

    const itemIndex = this.orderForm.items.findIndex(
      ({ id }) => parseInt(id) === 35
    );

    if (itemIndex === -1) return;

    const endpoint = `https://wcdlfckgi9.execute-api.us-east-2.amazonaws.com/v1/api/checkout/pub/orderform/${orderFormId}/items/${itemIndex}/price`;

    await fetch(endpoint, {
      method: 'PUT',
      body: JSON.stringify({
        price,
      }),
      headers: {
        Accept: 'application/json',
        'Content-Type': 'application/json',
      },
    });
  }

  async updateCheckoutAddress() {
    const { selectedAddresses } = this.orderDetails.shippingData;

    await this.cartService.updateCheckoutAddress(selectedAddresses[0]);
  }
}

document.addEventListener('DOMContentLoaded', () => {
  new InsuranceChangePage();
});
