/* eslint-disable max-len */
import { Logger } from './';

/**
 * Helper Abstraction class
 */
class Helper {
  /**
   * Helper constructor
   * @param {*} props
   */
  constructor(props) {
    this._logger = new Logger('Ensure');
    this.props = props || {
      language: navigator.language || navigator.userLanguage,
      currency: 'BRL',
      currencyLocale: 'pt-br',
      minimumFractionDigits: 2,
    };
  }

  /**
   * format currency
   * @param {*} value
   * @param {*} minimumFractionDigits
   * @return {*}
   */
  formatPrice(value) {
    value = value / 100;
    value = (value * 1).toFixed(
      Math.max(0, ~~this.props.minimumFractionDigits)
    );

    return new Intl.NumberFormat(this.props.currencyLocale, {
      style: 'currency',
      currency: this.props.currency,
      minimumFractionDigits: this.props.minimumFractionDigits,
    }).format(value);
  }

  static getParents(elem, selector) {
    // Element.matches() polyfill
    if (!Element.prototype.matches) {
      Element.prototype.matches =
        Element.prototype.matchesSelector ||
        Element.prototype.mozMatchesSelector ||
        Element.prototype.msMatchesSelector ||
        Element.prototype.oMatchesSelector ||
        Element.prototype.webkitMatchesSelector ||
        function(s) {
          let matches = (this.document || this.ownerDocument).querySelectorAll(
              s
            ),
            i = matches.length;

          while (--i >= 0 && matches.item(i) !== this) {}

          return i > -1;
        };
    }

    // Set up a parent array
    let parents = [];

    // Push each parent element to the array
    for (; elem && elem !== document; elem = elem.parentNode) {
      if (selector) {
        if (elem.matches(selector)) {
          parents.push(elem);
        }

        continue;
      }

      parents.push(elem);
    }

    // Return our parent array
    return parents;
  }

  static serializeForm(form) {
    // Setup our serialized data
    let serialized = [];

    // Loop through each field in the form
    for (let i = 0; i < form.elements.length; i++) {
      let field = form.elements[i];

      // Don't serialize fields without a name, submits, buttons, file and reset inputs, and disabled fields
      if (
        !field.name ||
        field.disabled ||
        field.type === 'file' ||
        field.type === 'reset' ||
        field.type === 'submit' ||
        field.type === 'button'
      )
        continue;

      // If a multi-select, get all selections
      if (field.type === 'select-multiple') {
        for (let n = 0; n < field.options.length; n++) {
          if (!field.options[n].selected) continue;

          serialized.push(
            encodeURIComponent(field.name) +
              '=' +
              encodeURIComponent(field.options[n].value)
          );
        }
      }

      // Convert field data to a query string
      else if (
        (field.type !== 'checkbox' && field.type !== 'radio') ||
        field.checked
      ) {
        serialized.push(
          encodeURIComponent(field.name) + '=' + encodeURIComponent(field.value)
        );
      }
    }

    return serialized.join('&');
  }

  static getFormData(form) {
    // Setup our serialized data
    let data = {};

    // Loop through each field in the form
    for (let i = 0; i < form.elements.length; i++) {
      let field = form.elements[i];

      // Don't serialize fields without a name, submits, buttons, file and reset inputs, and disabled fields
      if (
        !field.name ||
        field.disabled ||
        field.type === 'file' ||
        field.type === 'reset' ||
        field.type === 'submit' ||
        field.type === 'button'
      )
        continue;

      // If a multi-select, get all selections
      if (field.type === 'select-multiple') {
        for (let n = 0; n < field.options.length; n++) {
          if (!field.options[n].selected) continue;

          data[field.name] = field.options[n].value;
        }
      }

      // Convert field data to a query string
      else if (
        (field.type !== 'checkbox' && field.type !== 'radio') ||
        field.checked
      ) {
        data[field.name] = field.value;
      }
    }

    return data;
  }

  static getRandomID() {
    // Generate unique IDs for use as pseudo-private/protected names.
    // Similar in concept to
    // <http://wiki.ecmascript.org/doku.php?id=strawman:names>.
    //
    // The goals of this function are twofold:
    //
    // * Provide a way to generate a string guaranteed to be unique when compared
    //   to other strings generated by this function.
    // * Make the string complex enough that it is highly unlikely to be
    //   accidentally duplicated by hand (this is key if you're using `ID`
    //   as a private/protected name on an object).
    //
    // Use:
    //
    //     var privateName = ID();
    //     var o = { 'public': 'foo' };
    //     o[privateName] = 'bar';

    // Math.random should be unique because of its seeding algorithm.
    // Convert it to base 36 (numbers + letters), and grab the first 9 characters
    // after the decimal.

    return (
      '_' +
      Math.random()
        .toString(36)
        .substr(2, 9)
    );
  }

  static setCookie(cname, cvalue, exdays) {
    const date = new Date();

    date.setTime(date.getTime() + exdays * 24 * 60 * 60 * 1000);

    const expires = 'expires=' + date.toUTCString();

    document.cookie = `${cname}=${cvalue};${expires};path=/`;
  }

  static getCookie(cname) {
    const decodedCookie = decodeURIComponent(document.cookie);

    const cookie = decodedCookie
      .split(';')
      .filter(cookie => cookie.includes(cname))[0];

    return cookie || '';
  }

  static isInViewport(element) {
    const heightViewport =
      window.innerHeight || document.documentElement.clientHeight;

    const widthViewport =
      window.innerWidth || document.documentElement.clientWidth;

    const { top, right, bottom, left } = element.getBoundingClientRect();

    return (
      top >= 0 &&
      left >= 0 &&
      bottom <= heightViewport &&
      right <= widthViewport
    );
  }

  static removeAccents(str) {
    const mapAccents = {
      a: '|||||||',
      e: '|||||',
      i: '|||||',
      o: '|||||||',
      u: '|||||||',
      c: '|',
      n: '|',
    };

    for (let pattern in mapAccents) {
      str = str.replace(new RegExp(mapAccents[pattern], 'g'), pattern);
    }

    return str;
  }
}

export default Helper;
